@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Roboto', sans-serif;
	min-height: 100vh;
}

.wrap {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

.title {
	margin: 10px 0 0;
	text-align: center;
}

.container {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	max-width: 1000px;
}

.container img {
	object-fit: cover;
	margin: 10px;
	height: 300px;
	width: 300px;
	max-width: 100%;
}

